<template>
	<view>
		//dianji 
		<button type="default" @click.stop="readActivity()">click</button>
		//下面是动画
		<!-- @touchstart.stop="touchStart"
		@touchend.stop="touchEnd" -->
		<view
			:animation="animationData"
			
			class="hisNewActiviy"
			
			style="width: 90%;height: 0; position: fixed; bottom: 0; left: 50%;transform: translateX(-50%);background-color: #217f99;border-radius: 30rpx;z-index: -100;"
		>
			
			<!-- <button type="default" @click="show">点击</button> -->
				<view @click="show()" style="position: absolute;top: 0;z-index: 1000;">点击 </view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				animationData: {},
			}
		},
		methods: {
			show(){
				console.log("123");
			},
			onUnload() {
					this.animationData = {};
					// 页面关闭后清空数据
				},
				onload() {
					this.animation = uni.createAnimation();
					// 创建动画实例
				},
				readActivity(val) {
					
					this.content = val;
					var animation = uni.createAnimation({
						duration: 700,
						timingFunction: 'ease'
					});
			
					animation.height(600).step();
			
					this.animationData = animation.export();
				},
			
				touchStart(e) {
					if (e.touches.length == 1) {
						//设置触摸起始点水平方向位置
						this.startY = e.touches[0].clientY;
					}
				},
			
				touchEnd(e) {
					
					if (e.changedTouches.length == 1) {
						//手指移动结束后水平位置
						var endY = e.changedTouches[0].clientY;
						let diff = endY - this.startY;
						if (Math.abs(diff) > 100) {
							if (diff > 0) {
								
								console.log('下滑...');
								var animation = uni.createAnimation({
									duration: 700,
									timingFunction: 'ease'
								});
								animation.height(0).step();
								this.flag = !this.flag;
								this.animationData = animation.export();
							} else {
								console.log('上滑...');
							}
						}
					}
				}
			}
			
		}
	
</script>

